const reducer = (state, action) => {
  switch (action.type) {
    case "GET_NEW_USER":
      return {
        ...state,
        user: [...state.user, action.payload],
      };

    case "GET_USER_ONLINE":
      return {
        ...state,
        online: [action.payload],
      };

    default:
      return state;
  }
};

export default reducer;
